@import "../../global.scss";

.portfolio {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        font-size: 50px;

        // @include mobile{
        //     font-size: 20px;
        // }
    }

    ul {
        margin: 20px 0;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            margin: 10px 0;
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }

        .item {
            width: 900px;
            height: 450px;
            border-radius: 20px;
            border: 1px solid rgb(240, 239, 239);
            margin-top : 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;
            
            @include mobile{
                margin: 10px;
                width: 400px;
                height: 300px;
            }


            h3 {
                position: absolute;
                font-size: 20px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover {
                background-color: #F39689;

                img {
                    opacity: 0.2;
                    z-index: 0;
                }

            }
        }
    }
}